/**
 * mt-header
 * @module components/header
 * @desc 
 * @param {boolean} [router] - 
 * @param {String} [to] - 
 * @param {String} [leftIcontext] - 
 * @param {String} [backgroundColor] - header
 * @param {String} [color] - header
 * @param {String} [fontSize] - header
 * @param {String} [height] - header
 * @param {boolean} [fixed=false] - 
 * @param {string} [title] - 
 * @param {slot} [left] - 
 * @param {slot} [right] - 
 *
 * @example
 * <mt-header title="" fixed>
 *   <mt-button slot="left" icon="back" @click="handleBack"></mt-button>
 *   <mt-button slot="right" icon="more"></mt-button>
 * </mt-header>
 */
export default {
  name: 'mt-header',

  props: {
    router:Boolean,
    to:{
      type:String,
      default:"/"
    },
    leftIcontext:{
      type:String,
      default:""
    },
    backgroundColor:{
      type:String,
      default:"mt-bg-lv2"
    },
    color:{
      type:String,
      default:"mt-color-grey"
    },
    fontSize:{
      type:String,
      default:"18px"
    },
    height:{
      type:String,
      default:"44px"
    },
    fixed: Boolean,
    title: String
  }
};



