/**
 * mt-cell
 * @module components/cell
 * @desc 
 * @param {string|Object} [to] -  vue-router  to  router.push a  href 
 * @param {string} [icon] -  more, back .mintui-xxx
 * @param {string} [title] - 
 * @param {string} [label] - 
 * @param {boolean} [is-link=false] - 
 * @param {boolean} [arrowdefined=false] - slot
 * @param {boolean} [disabledcolor=false] - readonlydisabled
 * @param {string} [value] - 
 * @param {slot} -  value,  value 
 * @param {slot} [title] -  title,  title 
 * @param {slot} [icon] -  icon,  icon 
 *
 * @example
 * <mt-cell title="" icon="back" is-link value=""></mt-cell>
 * <mt-cell title="" icon="back">
 *   <div slot="value"></div>
 * </mt-cell>
 */

/**
 * @noteType component
 * @name Cell
 * @tagName mt-cell
 * @desc 
 * @wrapClassName mint-cell
 * @html
 * <mt-cell title=""></mt-cell>
 */
export default {
  name: 'mt-cell',

  props: {
    cellheight:{
      type:String,
      default:'50px'
    },
    to: [String, Object],
      /**
       * @noteType prop
       * @field icon
       * @desc 
       * @type select
       * @option
       [
       {
         "text": "",
         "value": ""
       },
       {
         "text": "",
         "value": "back"
       },
       {
         "text": "",
         "value": "more"
       }
       ]
       */
    icon: String,
      /**
       * @noteType prop
       * @field title
       * @desc 
       * @type input
       * @value 
       */
    title: String,
      /**
       * @noteType prop
       * @field label
       * @desc 
       * @type input
       * @value
       */
    label: String,
      /**
       * @noteType prop
       * @field isLink
       * @desc 
       * @type select
       * @valueType boolean
       * @value false
       * @option
       [
       {
         "text": "",
         "value": false
       },
       {
         "text": "",
         "value": true
       }
       ]
       */
    isLink: Boolean,
      /**
       * @noteType prop
       * @field value
       * @desc 
       * @type input
       * @value
       */
    arrowdefined: Boolean,
    disabledcolor: Boolean,
    value: {},
    id:'',
    wrapperpaddingleft:{
      type:String,
      default:''
    },
    wrapperpaddingright:{
      type:String,
      default:''
    },
    titlewidth:{
      type:String,
      default:'20%'
    },
    titlepaddingtop:String,
    titlepaddingright:String,
    titlepaddingbottom:String,
    titlepaddingleft:String
  },

  computed: {
    href() {
      if (this.to && !this.added && this.$router) {
        const resolved = this.$router.match(this.to);
        if (this.to === 'click') {
          this.$nextTick(() => {
            this.$el.addEventListener('click', this.handleClick);
          });
          return ''
        }
        if (!resolved.matched.length) return this.to;

        this.$nextTick(() => {
          this.added = true;
          this.$el.addEventListener('click', this.handleClick);
        });
        return resolved.fullPath || resolved.path;
      }
      
      return this.to;
    },
    isCell() {
      return false;
    },
    isGroupCell() {
      return !!this.findParentByName('mt-cell-group');
    },
    wrapperpaddingrightdefined() {
      return this.arrowdefined||this.isLink?"15px":"20px";
    }
  },

  methods: {
    handleClick($event) {
      $event.preventDefault();
      if (this.to != 'click') {
        this.$router.push(this.href);
      }else {
        this.$emit('cellClick',$event,this._props);
      }
    },
    findParentByName(name) {
      if (!this.parentGroup) {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name === name) {
            this.parentGroup = parent;
            break;
          }
          parent = parent.$parent;
        }
      }

      return this.parentGroup;
    }
  },

  created:function(){
    this.$nextTick(function(){
      let targetDom=this.$refs.cellValue;
      if(targetDom.firstChild.nodeName === "TEXTAREA"){
        targetDom.parentNode.style.alignItems="normal"
      }
    })
  }
};

/**
 * @noteType external
 * @content
 {
   "slot": [
          {
            "field": "icon",
            "desc": "",
            "caption": "",
            "option": [
              {
                "componentName": "Image",
                "attr": {
                  "height": "20px",
                  "width": "20px",
                  "smile-display": "inline-block"
                }
              }
            ]
          }
        ],
   "bindField": ":value"
 }
 */



