/**
 * mt-tabbar
 * @module components/tabbar
 * @desc  tab tab-item
 * @param {boolean} [fixed=false] - 
 * @param {*} value -  item component  id
 *
 * @example
 * <mt-tabbar v-model="selected">
 *   <mt-tab-item id="">
 *     <img slot="icon" src="http://placehold.it/100x100">
 *     <span slot="label"></span>
 *   </mt-tab-item>
 * </mt-tabbar>
 *
 * <mt-tabbar v-model="selected" fixed>
 *   <mt-tab-item :id="['', '']">
 *     <img slot="icon" src="http://placehold.it/100x100">
 *     <span slot="label"></span>
 *   </mt-tab-item>
 * </mt-tabbar>
 */
export default {
  name: 'mt-tabbar',

  props: {
    fixed: Boolean,
    value: {}
  }
};



