/**
 * mt-navbar
 * @module components/navbar
 * @desc  tab tab-item
 *
 * @param {boolean} [fixed=false] - 
 * @param {*} selected -  item component  value
 *
 * @example
 * <mt-navbar :selected.sync="selected">
 *   <mt-tab-item value="">
 *     <span slot="label"></span>
 *   </mt-tab-item>
 * </mt-navbar>
 *
 * <mt-navbar :selected.sync="selected" fixed>
 *   <mt-tab-item :value="['', '']">
 *     <span slot="label"></span>
 *   </mt-tab-item>
 * </mt-navbar>
 *
 */

/**
 * @noteType component
 * @name Navbar
 * @tagName mt-navbar
 * @desc 
 * @type tab
 * @wrapClassName mint-navbar
 * @html
 * <mt-navbar v-model="selected"></mt-navbar>
 */
export default {
  name: 'mt-navbar',
  props: {
    fixed: Boolean,
    value: {},
  }
};

/**
 * @noteType external
 * @content
 {
    "item": {
      "componentName": "TabItem",
      "count": 2,
      "showProp": "space"
    },
    "mock": {
      "selected": "0"
    },
    "modelName": "selected",
    "relation": {
      "componentName": "TabContainer"
    }
 }
 */



